.delay-500{
  transition-delay: 500ms;
}

.duration-200{
  transition-duration: 200ms;
}

.duration-300{
  transition-duration: 300ms;
}

.duration-500{
  transition-duration: 500ms;
}

.duration-1000{
  transition-duration: 1000ms;
}

.ease-in-out{
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

@media (min-width: 640px){
  .sm\:max-w-xl{
    max-width: 36rem;
  }

  .sm\:py-12{
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}

@media (min-width: 768px){
  .md\:mx-0{
    margin-left: 0px;
    margin-right: 0px;
  }

  .md\:mt-10{
    margin-top: 2.5rem;
  }

  .md\:mb-auto{
    margin-bottom: auto;
  }

  .md\:flex{
    display: flex;
  }

  .md\:hidden{
    display: none;
  }

  .md\:h-40{
    height: 10rem;
  }

  .md\:min-h-screen{
    min-height: 100vh;
  }

  .md\:w-40{
    width: 10rem;
  }

  .md\:w-8\/12{
    width: 66.666667%;
  }

  .md\:flex-col{
    flex-direction: column;
  }

  .md\:items-start{
    align-items: flex-start;
  }

  .md\:text-center{
    text-align: center;
  }

  .md\:text-7xl{
    font-size: 4.5rem;
    line-height: 1;
  }
}

